import { useRef, useState } from "react";
import classes from "./VideoPreview.module.scss";

function VideoPreview({ imageName, id, previewClicked, text, isMobile }) {
  const [isText, setIsText] = useState(false);
  const iconRef = useRef();

  function opacityAndShowText() {
    iconRef.current.style.opacity = 1;
    setIsText(true);
  }
  function opacityAndHideText() {
    iconRef.current.style.opacity = 0.5;
    setIsText(false);
  }

  let textClass =
    isText || isMobile
      ? `${classes.text} ${classes.reveale}`
      : `${classes.text} ${classes.hidden}`;

  const textElement = <div className={textClass}>{text}</div>;

  return (
    <div
      className={classes.wrapper}
      style={{ height: isMobile ? "90vh" : "30vh" }}
      onClick={() => previewClicked(id)}
    >
      {textElement}
      <div
        onMouseOver={opacityAndShowText}
        onMouseOut={opacityAndHideText}
        className={classes.image}
        style={{ backgroundImage: "url(" + imageName + ")" }}
      >
        <div
          ref={iconRef}
          className={classes.icon}
          style={{
            maskImage: "url('/Icons/play.png')",
            webkitMaskImage: "url('/Icons/play.png')",
          }}
        ></div>
      </div>
    </div>
  );
}

export default VideoPreview;
